@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
:root{
  --main-bg-color: #191923;
  --primary-1: #1e1e28;
  --primary-2:#20202a;
  --secondary-1: linear-gradient(
    159deg, #2d2d39 0%,
            #2b2b35 100%);
  --secondary-2: linear-gradient(
    159deg, rgba(37, 37, 50, 0.98) 0%,
            rgba(35, 35, 45, 0.98) 100%);
  --text-color-1: #fafafc;
  --text-color-2: #cacace;
  --text-color-3: #8c8c8e;
  --button-bg: #ffc107;
  --box-shadow: 0 1px 4px 0 rgba(15, 15, 20, 0.1);
  --box-shadow-2: 0 3px 8px 0 rgba(15, 15, 20, 0.2);
  --overlay-1: linear-gradient(180deg,
  rgba(30, 30, 40, 0.93) 0%, 
  rgba(30, 30, 40, 0.96) 70%, 
  rgba(30, 30, 40, 0.99) 80%, 
  #1e1e28 100%);
  --overlay-2: linear-gradient(90deg, 
  rgba(45, 45, 58, 0.9) 10%, 
  rgba(45, 45, 58, 0.9) 25%, 
  rgba(45, 45, 58, 0.8) 35%, 
  rgba(45, 45, 58, 0.9) 50%, 
  rgba(45, 45, 58, 0.8) 75%, 
  rgba(43, 43, 53, 0.9) 100%);
}
*{
 
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  
}
input,
textarea,
button,
select,
a,svg, div {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
body{
  color: #fafafc;
  color: var(--text-color-1);
  overflow-x: hidden;
}
a{
  text-decoration: none;
}
.main-app{
  position: relative;
  background-color: #191923;
  background-color: var(--main-bg-color);
}
.main-app-wrapper{
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  background: #1e1e28;
  background: var(--primary-1);
}
.main-app-container{
  position: relative;
  width: 100%;
  
  
}
.items{
  position: relative;
  width: 100%;
}

/*======================Navigation bar=========================*/
.navbar{
  position: fixed;
  z-index: 96;
  width: 100%;
  height: 60px;
  background: #20202a;
  background: var(--primary-2);
  box-shadow: 0 3px 8px 0 rgba(15, 15, 20, 0.2);
  padding: 20px 30px;
  display: none;
}
.navbar svg{
  cursor: pointer;
}

/*============================Menu bar styles==============================*/
.menu-bar{
  width: 60px;
  height: 100vh;
  position: fixed;
  transition: all 0.4s ease-out;
  z-index: 98;
  box-shadow: 0 3px 8px 0 rgba(15, 15, 20, 0.2);
  box-shadow: var(--box-shadow-2);
}
.menu-bar ul li{
  opacity: 0;
  transition-delay: 0s;
  transition-delay: initial;
}
.menu-bar.active{
  width: 200px;
}

.menu-bar.active .menu-items-container ul li{
  transform: translateX(0);
  opacity: 1;
}
.menu-bar .menu-items-container ul li:nth-child(1){
  transition-delay: 0.1s;
}
.menu-bar .menu-items-container ul li:nth-child(2){
  transition-delay: 0.15s;
}
.menu-bar .menu-items-container ul li:nth-child(3){
  transition-delay: 0.2s;
}
.menu-bar .menu-items-container ul li:nth-child(4){
  transition-delay: 0.25s;
}
.menu-bar .menu-items-container ul li:nth-child(5){
  transition-delay: 0.3s;
}
.menu-bar .menu-items-container ul li{
  transform: translateX(-200px);
  transition: all 0.4s ease-in;
}
.menu-bar-container{
  background: #20202a;
  background: var(--primary-2);
  height: 100vh;
}
.menu-head{
  background: linear-gradient(
    159deg, rgba(37, 37, 50, 0.98) 0%,
            rgba(35, 35, 45, 0.98) 100%);
  background: var(--secondary-2);
  padding: 1rem;
  padding-right: 2rem;
  text-align: right;
}
.menu-head svg{
  cursor: pointer;
}
.menu-items-container{
  padding-right: 2rem;
  text-align: right;
  margin-top: 100px;
}
.menu-items-container ul a{
  text-decoration: none;
  color: #cacace;
  color: var(--text-color-2);
  font-weight: 500;
  font-size: 1rem;
  line-height: 2.5;
  transition: all 1s ease-in;
}

.menu-items-container ul a:hover{
  color: #fafafc;
  color: var(--text-color-1);
}

/*============================Info bar styles==============================*/
ul{
  list-style: none;
}

.info-bar{
  position: absolute;
  right: 0;
  height: 100vh;
  width: 290px;
  color: #cacace;
  color: var(--text-color-2);
  z-index: 99;
}
.info-bar-wrapper{
  position: fixed;
  right: auto;
  left: auto;
  top: auto;
  bottom: auto;
  height: 100%;
  width: 290px;
  background: #20202a;
  background: var(--primary-2);
}
.info-bar-container{
  width: 290px;
  height: 100%;
  position: relative;
}
.info-bar-header{
  height: 235px;
  width: 100%;
  background: linear-gradient(
    159deg, rgba(37, 37, 50, 0.98) 0%,
            rgba(35, 35, 45, 0.98) 100%);
  background: var(--secondary-2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding:1rem;
  box-shadow: 0 1px 4px 0 rgba(15, 15, 20, 0.1);
  box-shadow: var(--box-shadow);
  position: absolute;
  top: 0;
}
.info-bar-img{
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.live{
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: #ffc107;
  background-color: var(--button-bg);
  position: absolute;
  bottom: 8px;
  right: 7px;
  -webkit-animation: animate 1s linear infinite;
          animation: animate 1s linear infinite;
  cursor: pointer;
}
.live::before{
  content: "Available for hire";
  width: 80px;
  height: 30px;
  position: relative;
  right: -20px;
  background: #1e1e28;
  background: var(--primary-1);
  font-size: 10px;
  padding: 4px;
  text-align: center;
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0,0,0,.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition:all 0.5s ease-in-out;
}
.live:hover:before{
  opacity: 1;
}

@-webkit-keyframes animate{
  0%{
    box-shadow: 0 0 0 0 rgba(255, 255, 255,.6), 0 0 0 0 rgba(255, 255, 255,.6);
  }
  25%{
    box-shadow: 0 0 0 3px rgba(255, 255, 255,.3), 0 0 0 3px rgba(255,255, 255,.3);
  }
  50%{
    box-shadow: 0 0 0 2px rgba(255, 255, 255,.2), 0 0 0 2px rgba(255, 255, 255,.1);
  }
  100%{
    box-shadow: 0 0 0 1px rgba(255, 255, 255,.1), 0 0 0 1px rgba(255, 255, 255,.1)
  }
}

@keyframes animate{
  0%{
    box-shadow: 0 0 0 0 rgba(255, 255, 255,.6), 0 0 0 0 rgba(255, 255, 255,.6);
  }
  25%{
    box-shadow: 0 0 0 3px rgba(255, 255, 255,.3), 0 0 0 3px rgba(255,255, 255,.3);
  }
  50%{
    box-shadow: 0 0 0 2px rgba(255, 255, 255,.2), 0 0 0 2px rgba(255, 255, 255,.1);
  }
  100%{
    box-shadow: 0 0 0 1px rgba(255, 255, 255,.1), 0 0 0 1px rgba(255, 255, 255,.1)
  }
}
img.avatar{
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}
.avatar-name{
  color: #fafafc;
  color: var(--text-color-1);
  font-weight: 800;
  margin-top: 0.5rem;
}
.description{
  margin-top: 1rem;
  text-align: center;
}
.description h5{
  font-weight: 500;
  margin-top: 0.2rem;
}

.info-bar-details{
  height: 100vh;
  padding: 240px 1rem 50px 1rem;
  letter-spacing: 1px;
  font-weight: 400;
  font-size: 13px;
}

.basic-info{
  padding: 0.5rem;
  
}
.basic-info ul li{
  display: flex;
  justify-content: space-between;
  padding-bottom: 3px;
}
.skills, .download-resume{
  line-height: 1.2rem;
  padding: 1rem 0.5rem;
}
.download-resume{
  cursor: pointer;
}
.skills h5{
  font-weight: 800;
}
.top-skills{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 8px;
  gap: 8px;
}
.skill-item{
  border: 2px solid #ffc107;
  border: 2px solid var(--button-bg);
  padding: 3px;
  border-radius: 8px;
  text-align: center;
  font-size: 10px;
}
.info-bar-footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.5rem;
  height: 50px;
  background: linear-gradient(
    159deg, rgba(37, 37, 50, 0.98) 0%,
            rgba(35, 35, 45, 0.98) 100%);
  background: var(--secondary-2);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
}
.divider{
  background: #8c8c8e;
  width: 100%;
  height: 1px;
  opacity: 0.3;
}


/*============================Content styles==============================*/
.content{
  padding-left: 60px;
  padding-right: 290px;
  transition: all 0.4s ease-in-out;
  /* transition-delay: 0.1s; */
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.content.active{
  transform: translateX(140px);
  transition: all 0.4s ease-in-out;
  overflow-x: hidden;
}
.content-wrapper{
  position: relative;
  height: 100%;
  overflow-x: hidden;
  
  
}
.top-bg{
  background-image: url(/static/media/img-3.52255f1e.jpg);
  height: 400px;
  background-position: center;
  background-size: cover;
}
.top-bg-overlay{
  background-image: linear-gradient(180deg,
  rgba(30, 30, 40, 0.93) 0%, 
  rgba(30, 30, 40, 0.96) 70%, 
  rgba(30, 30, 40, 0.99) 80%, 
  #1e1e28 100%);
  background-image: var(--overlay-1);
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.main-bg-wrapper{
  padding: 2rem 2rem 0 2rem;
}

.main-bg{
  background-image: url(/static/media/img-3.52255f1e.jpg);
  height: 400px;
  width: 100%;
  background-size: cover;
  background-position: center;
  box-shadow: 10px 10px 8px 5px rgba(15, 15, 20, 0.2);
}
.main-bg-overlay{
  
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(90deg, 
  rgba(45, 45, 58, 0.9) 10%, 
  rgba(45, 45, 58, 0.9) 25%, 
  rgba(45, 45, 58, 0.8) 35%, 
  rgba(45, 45, 58, 0.9) 50%, 
  rgba(45, 45, 58, 0.8) 75%, 
  rgba(43, 43, 53, 0.9) 100%);
  background-image: var(--overlay-2);
 
}
.main-bg-overlay .title{
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 5rem;
  font-size: 32px;
  font-weight: 800;
}
.main-bg-overlay .title h3{
  font-size: clamp(24px, 2.5vw, 32px);
}
.main-bg-overlay .title h4{
  font-size: clamp(18px, 2.5vw, 28px);
}
.main-bg-overlay .title p span{
  color: #ffc107;
  color: var(--button-bg);
}
.main-bg-overlay .title p{
  font-size: clamp(10px, 1.5vw, 18px);
  font-weight: 600;
  color: #cacace;
  color: var(--text-color-2);
  margin-top: 8px;
}


.buttons{
  padding-top: 10px;
}
.bg-button{
  padding: 16px 35px;
  background: none;
  border: 1px solid #ffc107;
  border: 1px solid var(--button-bg);
  color: #fafafc;
  color: var(--text-color-1);
  font-weight: 600;
  font-size: 12px;
  cursor: pointer;
}
.bg-button:nth-child(2){
  margin-left: 8px;
}


/*==================================Tools, Languages and Socials=================={=======*/

.section-wrapper, .socials-wrapper{
  padding: 1rem 2rem 2rem;
}
.section-wrapper h4,
.socials-wrapper h4{
  color: #ffc107;
  color: var(--button-bg);
  font-size: 14px;
  font-weight: 700;
}
.skills-container,
.socials-container{
  padding-top: 0.5rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-gap: 12px;
  gap: 12px;
  
  
}
.skills-container .skill,
.socials-container .social{
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #cacace;
  border: 1px solid var(--text-color-2);
  padding: 10px;
  color: #fafafc;
  color: var(--text-color-1);
}
.skills-container .skill span,
.socials-container .social span{
  margin-left: 5px;
  font-size: 13px;
}
.socials-wrapper{
  padding-top: 0;
}

/*==================================Projects=========================*/

.projects-header{
  padding: 2rem 2rem 0.5rem;
  font-size: 14px;
  font-weight: 700;
  color: #ffc107;
  color: var(--button-bg);
  
}

.projects-container{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding: 0.5rem 2rem;
  color: #cacace;
  color: var(--text-color-2);
  line-height: 1.4;
  letter-spacing: 1.3px;
}
.project{
  background: linear-gradient(
    159deg, #2d2d39 0%,
            #2b2b35 100%);
  background: var(--secondary-1);
  box-shadow: 0 3px 8px 0 rgba(15, 15, 20, 0.2);
  box-shadow: var(--box-shadow-2);
}
.project-wrapper{
  height: 100%;
  padding: 1rem 1rem 0;
  display: flex;
  flex-direction: column;
}
.project-title, .project-details,
.project-stack, .project-links{
  padding: 0 0.5rem;
}
.project-title{
  color: #fafafc;
  color: var(--text-color-1);
  font-size: 12px;
}
.project-details{
  padding-top: 0.5rem;
}

.project-stack, .project-links{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(70px, 80px));
  grid-gap: 6px;
  gap: 6px;
  white-space: nowrap;
}
.project-stack{
  padding-top: 1rem;
  padding-bottom: 1rem;
  
}
.project-links{
  padding-top: 1rem;
  padding-bottom: 1rem;
  border-top: 1px solid #8c8c8e;
  border-top: 1px solid var(--text-color-3);
  margin-top: auto;
}

.stack-item, .link-item{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px 5px;
  border: 2px solid #8c8c8e;
  border: 2px solid var(--text-color-3);
  font-size: 12px;
  border-radius: 10px;
}
.link-item{
  cursor: pointer;
  border-radius: 0;
  border-radius: initial;
}
.link-item a{
  color: #cacace;
  color: var(--text-color-2);
  display: flex;
  justify-content: center;
  align-items: center;
}
.link-item:nth-child(2){
  border: 2px solid #ffc107;
  border: 2px solid var(--button-bg);
}
.stack-item span, .link-item span{
  margin-left: 5px;
}



/*==================================Experiences and Education=========================*/
.work-education{
  margin-top: 2rem;
}
.we-wrapper{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  padding-left: 2rem;
  padding-right: 2rem;
  line-height: 1.4;
  letter-spacing: 1px;
}
.work-wrapper> h4, .education-wrapper> h4{
  font-size: 14px;
  padding-left: 2rem;
  color: #ffc107;
  color: var(--button-bg);
}
.work-wrapper .work, .education-wrapper .education{
  border-left: 5px solid #191923;
  border-left: 5px solid var(--main-bg-color);
  padding-left: 1.5rem;
  margin-top: 1rem;
}
.item{
  margin-bottom: 1rem;
  background: linear-gradient(
    159deg, #2d2d39 0%,
            #2b2b35 100%);
  background: var(--secondary-1);
  box-shadow: 0 3px 8px 0 rgba(15, 15, 20, 0.2);
  box-shadow: var(--box-shadow-2);
  padding: 0.5rem 1rem 1rem 1rem;
  position: relative;
}
.item::before{
  content: "";
  position: absolute;
  left: -38px;
  top: 0px;
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #1e1e28;
  background: var(--primary-1);
  border-width: 3px;
  border-style: solid;
  border-color: #ffc107;
  border-color: var(--button-bg);
  border-image: none;
  border-image: initial;
}
.item::after{
  content: "";
  position: absolute;
  left: -32px;
  top: 6px;
  display: inline-block;
  width: 10px;
  height: 10px;
  z-index: 10;
  border-radius: 50%;
  background: #ffc107;
  background: var(--button-bg);
}
.item .arrow{
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-top:10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid transparent;
  border-right: 10px solid #2b2b35;
  right: 100%;
  top: 5px;
}
.item-wrapper{
 
}
.item-wrapper .top-item{
  display: flex;
  justify-content: space-between;
  
  
}
.top-item .item-location{
  font-weight: 600;
  font-size: 14px;
}
.top-item .item-role{
  color: #8c8c8e;
  color: var(--text-color-3);
  font-weight: lighter;
  font-size: 12px;
  margin-top: 5px;
}
.item-wrapper .top-item .duration{
  background: #20202a;
  background: var(--primary-2);
  padding: 0 15px;
  border-radius: 20px;
  font-size: 12px;
  color: #cacace;
  color: var(--text-color-2);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 23px;
  white-space: nowrap;
}
.item-description p{
  font-size: 15px;
  color: #cacace;
  color: var(--text-color-2);
}
.technologies>h4{
  color: #fafafc;
  color: var(--text-color-1);
  font-size: 14px;
}
.technologies p{
  font-size: 13px;
}
.top-item, .item-description, .technologies{
  margin-top: 10px;
}

/*===========================Form=====================*/
form{
  background: linear-gradient(
    159deg, #2d2d39 0%,
            #2b2b35 100%);
  background: var(--secondary-1);
  padding: 2rem;
}
.form-container{
  padding: 3rem 2rem 0;
}
.form-container label{
  padding: 16px;
  background: #20202a;
  background: var(--primary-2);
  height: 50px;
}
.message-container label{
  height: 150px;
}
.name-container, .email-container, .message-container{
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}


.form-container input{
  padding: 10px;
  height: 50px;
  flex: 1 1;
  background: linear-gradient(
    159deg, rgba(37, 37, 50, 0.98) 0%,
            rgba(35, 35, 45, 0.98) 100%);
  background: var(--secondary-2);
  border: none;
  color: #fafafc;
  color: var(--text-color-1);
  min-width: 100px;
}
.form-container textarea{
  height: 150px;
  padding: 10px;
  flex: 1 1;
  background: linear-gradient(
    159deg, rgba(37, 37, 50, 0.98) 0%,
            rgba(35, 35, 45, 0.98) 100%);
  background: var(--secondary-2);
  border: none;
  color: #fafafc;
  color: var(--text-color-1);
}
.form-container input::-webkit-input-placeholder, .form-container textarea::-webkit-input-placeholder{
  color: #cacace;
  color: var(--text-color-2);
}
.form-container input:-ms-input-placeholder, .form-container textarea:-ms-input-placeholder{
  color: #cacace;
  color: var(--text-color-2);
}
.form-container input::-ms-input-placeholder, .form-container textarea::-ms-input-placeholder{
  color: #cacace;
  color: var(--text-color-2);
}
.form-container input::placeholder, .form-container textarea::placeholder{
  color: #cacace;
  color: var(--text-color-2);
}
.form-text{
  padding-bottom: 10px;
  font-size: 20px;
  color: #ffc107;
  color: var(--button-bg);
}
.form-button{
  padding: 10px;
  background: #ffc107;
  background: var(--button-bg);
  border: none;
  width: 180px;
  height: 50px;
  color: #1e1e28;
  color: var(--primary-1);
  font-weight: 600;
}

/*===========================Footer===============================*/

.footer-container{
  margin-top: 100px;
  padding: 0 2rem 3rem;
  text-align: center;
  color: #cacace;
  color: var(--text-color-2);
  font-size: 14px;
  font-weight: bold;
}
.footer-container p:nth-child(2){
  margin-top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer-container p span{
  margin-left: 5px;
}
/*===========================Media Queries===============================*/

@media screen and (min-width:768px){
  .skills-container, .socials-container{
    padding-right: 15rem;
  }
}
@media screen and (max-width:1024px){
  .content{
    padding: 60px 0 0;
  }
  .content.content.active{
    transform: none;
    transform: initial;
  }
  .navbar{
    display: flex;
    justify-content: space-between;
  }
  .navbar svg:nth-child(2){
    transition: all 0.5s ease-in-out;
  }
  .navbar.show-info svg:nth-child(2){
    transform: translateX(-270px);
  }
  .navbar svg:nth-child(1){
    transition: all 0.5s ease-in-out;
  }
  .navbar.show-info svg:nth-child(1){
    visibility: hidden;
  }
  .navbar.hide-menu svg:nth-child(2){
    visibility: hidden;
  }
  .info-bar{
    position: fixed;
    right: -290px;
    left: auto;
    transition: all 0.5s ease-in-out;
  }
  .info-bar.active{
    right: 0;
  }
  .menu-bar{
    transform: translateX(-100px);
  }
  .menu-bar.active{
    transform: none;
    transform: initial;
  }
  
}

@media screen and (max-width:768px){
  .projects-container{
    grid-template-columns: 1fr;
  }
  .we-wrapper{
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width:576px){
  .main-bg-wrapper{
    padding: 1rem 1rem 0 1rem;
  }
  .projects-container, .projects-header,
  .section-wrapper, .we-wrapper,
  .socials-wrapper, .form-container, form{
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

@media screen and (max-width:355px){
  .main-bg-overlay .title .buttons{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
  }
  .bg-button:nth-child(2){
    margin-left: 0;
    margin-left: initial;
    margin-top: 8px;
  }
}

@media screen and (max-width:300px){
  .item::before, .item::after, .item .arrow{
    display: none;
  }
  .work-wrapper .work, .education-wrapper .education{
    border-left: none;
    padding-left: 0;
    
  }
}
